import React, { useContext } from 'react';
import Context from '../utils/context'

const Controls = () => {
  const context = useContext(Context);
  
  return (
      <div style={{ padding: '1rem', background: '#D6F3FF' }}>
        <h1>Controls</h1>
        <div style={{ display: 'flex', marginTop: '1rem' }}>
          <input
            readOnly
            value={context.tasks[context.selected].name}
            placeholder="Selected task name"
            style={{ fontSize: '1rem' }}
            data-testid="selected-task-field"
          />
          <button
            style={{ marginLeft: '1rem' }}
            disabled={context.stageid>0?false:true}
            data-testid="move-back-btn"
            onClick={()=>context.movebackward()}
          >
            Move back
          </button>
          <button
            style={{ marginLeft: '1rem' }}
            disabled={context.stageid<3?false:true}
            data-testid="move-forward-btn"
            onClick={()=>context.moveforward()}
          >
            Move forward
          </button>
        </div>
      </div>
    )
  
}

export default Controls;



