import React, { Component } from 'react';
import './App.css';

import Controls from './components/Controls';
import Board from './components/Board';
import Context from './utils/context'

const NUM_STAGES = 4;

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      tasks: [
          { name: 'task 0', stage: 0 },
          { name: 'task 1', stage: 0 },
          { name: 'task 2', stage: 0 },
          { name: 'task 3', stage: 0 },
          { name: 'task 4', stage: 1 },
          { name: 'task 5', stage: 1 },
          { name: 'task 6', stage: 1 },
          { name: 'task 7', stage: 2 },
          { name: 'task 8', stage: 2 },
          { name: 'task 9', stage: 3 },
      ],
     selectedtask:0 ,
    };
    this.stagesNames = ['Backlog', 'To Do', 'Ongoing', 'Done'];
  }

  render() {
    
    const { tasks,selectedtask } = this.state;

    let stagesTasks = [];
    for (let i = 0; i < NUM_STAGES; ++i) {
      stagesTasks.push([]);
    }
    for (let task of tasks) {
      const stageId = task.stage;
      stagesTasks[stageId].push(task);
      
    }
    
    const moveforward=()=>{
      const elementsIndex = selectedtask
      let newArray = [...this.state.tasks]
      var prevState=this.state.tasks[elementsIndex].stage;
      newArray[elementsIndex] = {...newArray[elementsIndex], stage: prevState+1}
      this.setState({
      tasks: newArray,
      });
    }
    const movebackward=()=>{
      const elementsIndex = selectedtask
      let newArray = [...this.state.tasks]
      var prevState=this.state.tasks[elementsIndex].stage;
      newArray[elementsIndex] = {...newArray[elementsIndex], stage: prevState-1}
      this.setState({
      tasks: newArray,
      });
    }
    
    this.setSelectedtask=(name)=>{
      const elementsIndex = this.state.tasks.findIndex(task => task.name === name )
      this.setState({selectedtask:elementsIndex})
    }
    
        const stageid=this.state.tasks[selectedtask].stage;

    return (
      <div className="App">
        <Context.Provider 
        value={{
          setSelectedtask1:this.setSelectedtask,
          selected:selectedtask,
          stageid:stageid,
          tasks:tasks,
          moveforward:()=>moveforward(selectedtask),
          movebackward:()=>movebackward(selectedtask),
        }}
        >
        <Controls />
        <Board
          stagesTasks={stagesTasks}
          stagesNames={this.stagesNames}
        />
        </Context.Provider>
      </div>
    );
  }
}

export default App;



